body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.movie-card {
  background-color: white;
  border: 1px solid rgba(111, 26, 7, 0.2);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 5px;
  overflow: hidden;
  width: 300px;
  color: white;
  transition: 0.2s;
  box-shadow: 0px 0px 40px rgb(0, 0, 0);
}

.movie-card:hover {
  width: 335px;
  border: 1px solid rgb(255, 255, 255);
  color: black;
  font-weight: 00;
}

.movie-card:hover .card-title {
  display: none;
}

.movie-card:hover .movie-card-body {
  display: block;
}

.movie-card:hover .movie-card-subtitle {
  color: white;  
}

.movie-card-image {
  width: 100%;
}

.movie-card-body {
  flex: 1 1 auto;
  height: 200px;
  padding: 20px;
  display: none;
  transition: 0.2s;
  background-color: rgb(255, 144, 0);;
}

.movie-card-title {
  font-size: 24px;
  font-weight: 500;
  margin: 0 0 12px;
}

.movie-card-subtitle {
  color: #6c757d;
  font-size: 16px;
  font-weight: 500;
  margin: 0 0 8px;
}

.movie-card-storyline {
  font-size: 14px;
  text-align: justify;
  max-height: 100px;
  word-wrap: break-word;
}

.movie-card-link {
  color: rgb(255, 255, 255);
  text-decoration: none;
  background-color: whitesmoke;
  padding: 5px;
  border-radius: 50px;
  background-color: rgb(102, 0, 0);
}

.card-title {
  color: black;
  font-weight: 900;
  position: absolute;
  background-color: rgba(255, 255, 255, 0.6);
  padding: 5px 20px 5px 5px;
  border-top-right-radius: 50px;
}
