.loading-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc( 90vh - 40px);
}
.movie-list {
  display: flex;
  flex-flow: row wrap;
  padding: 48px 30px;
  justify-content: space-evenly;
}
.details-content {
  display: flex;
  justify-content: space-between;
  min-height: calc(90vh - 40px);
}

.image-hero {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
}

.img-content {
  order: 1;
  width: 80%; 
  position: relative;
}

.fade-img {
  width: 100%;
  position: absolute;
  height: 100%;
  background: linear-gradient(88deg, rgb(0, 0, 0), rgba(255, 255, 255, 0.1));
  z-index: 0;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.info-details {
  display: flex;
  flex-direction: column;
  flex-wrap: row wrap;
  padding: 0 40px 20px 30px ;
  background-color: black;
  justify-content: space-evenly;
  max-width: 46%;
}

.title-detail {
  color: white;
  font-size: 70px;
  text-shadow: 3px 3px rgb(95, 2, 31);
  -webkit-text-stroke-color: #000;
  -webkit-text-stroke-width: 1px;


}

.subtitle-detail {
  color: rgb(255, 255, 255);
  font-size: 40px;
  opacity: 0.8;
}

.storyline-detail {
  color: white;
  font-size: 20px;
}

.genre-detail {
  color: white;
  font-size: 30px;
}

.rating-detail {
  color: white;
  font-size: 30px;
}

.buttons-detail {
  display: flex;
  justify-content: space-evenly;
}

.edit-btn, .back-btn, .rm-btn {
  background-color: rgb(102, 0, 0);
  color: white;
  padding: 5px;
  border-radius: 10px;
  text-decoration: none;
  box-shadow: 1px 1px 2px rgb(255, 144, 0);
}

.bi-thermometer-low, .bi-thermometer-half, .bi-thermometer-high {
  font-size: 30px;
}
